import config from '@/components/config/config.vue'

import luPopupWrapper from "@/components/lu-popup-wrapper/lu-popup-wrapper.vue";
export default {
	 components: {
	        luPopupWrapper
	    },
	props: {
		list: { // 数据列表
			type: Array,
			default () {
				return []
			}
		}
	},
	data() {
		return {
			/* 搜索条默认选中项 */
			index: 1,
			/* 搜索条综合是否展开 */
			c: false,
			/* 搜索条综合搜索选项 */
			cList: ['综合', '最新上架', '价格最低', '价格最高', '评价最多'],
			/* 搜索条 综合显示文本 */
			syntText: '综合',
			/* 列表类型 1一行一个，2一行两个*/
			listType: 1,
			/* 更多过滤 */
			showFilter: false,
			
			
			//底部弹出框
			type:"bottom",// left right top bottom center
			transition:"slider",//none slider fade
			backgroundColor:'#FFF',
			active:false,
			height:"100%",
			width:"100%",
			popupId:1,
			maskShow:true,
			maskClick:true,
			
			//底部弹出框内容
			winid:0,
			winmid:0,
			winmname:"Nil",
			wingrade:0,
			winbirth:"",
			winimage:"Nil",
			wintitle:"Nil",
			wintime:"",
			windescribe:"Nil",
			winaddress:"Nil",
			winlable:"Nil",
			winphone:""
			
		}
	},
	methods: {
		watchdetail(item){
			var value = uni.getStorageSync('token');
			if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/transporter',
			  		method:"GET",
			  	    header: {
			  		   'Authorization' : value,
			  	    },
					data:{
						'id':item.mid, //0代表所有id
					},
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
							var date=new Date(jsondata.data.created_at*1000)
							var Y = date.getFullYear() + '-'
							var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
							var D = (date.getDate()< 10 ? '0'+date.getDate() :date.getDate() )+ ' '
							this.winbirth=Y+M+D
							
							var date1=new Date(item.time*1000)
							var Y1 = date1.getFullYear() + '-'
							var M1 = (date1.getMonth()+1 < 10 ? '0'+(date1.getMonth()+1) : date1.getMonth()+1) + '-'
							var D1 = (date1.getDate()< 10 ? '0'+date1.getDate() :date1.getDate() )+ ' '
							this.wintime=Y1+M1+D1
							
							
							this.winid=item.id,
							this.winmid=item.mid,
							this.winmname=jsondata.data["nickname"],  //
							this.winphone=jsondata.data["phone"],  //
							this.wingrade=10,    //
							this.winimage=item.image,
							this.wintitle=item.title,
							this.windescribe=item.describe,
							this.winaddress=item.address,//
							this.winlable=item.lable//
							
							this.width ="100%";
							this.height ="70%";
							this.transition = "slider";
							this.type = "bottom";
							this.show();
							
							this.addwatchhistory()
							
							
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}	
													
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  config.txtshow("未登录")	
			}
			
																	
			
		},
		collect(){
			var value = uni.getStorageSync('token');
			if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/commodity/collect',
			  		method:"POST",
			  	    header: {
			  		   'Authorization' : value,
			  	    },
					data:{
						'cid':this.winid, //0代表所有id
					},
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
							config.txtshow('收藏成功')								
			  			}else if(jsondata.code=='1'){
							config.txtshow('已收藏')
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}														
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  config.txtshow("未登录")	
			}
		},
		chat(){
			uni.navigateTo({
				url:'../user/chat?id='+this.winmid+'&name='+this.winmname
			})
		},
		order(){
			uni.navigateTo({
			    url: '../../pages/order/add?id='+this.winid
			});
		},
		addwatchhistory(){
			var value = uni.getStorageSync('token');
			if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/commodity/watchhistory',
			  		method:"POST",
			  	    header: {
			  		   'Authorization' : value,
			  	    },
					data:{
						'cid':this.winid, //0代表所有id
					},
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
							//console.log('增加浏览记录成功')
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}														
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  config.txtshow("未登录")	
			}
		},
		
		
		
		
		
		show: function() {
		            this.$refs.luPopupWrapper.show();
		        },
		 close: function() {
		            this.$refs.luPopupWrapper.close();
		        },
		 closeCallback:function() {
		            // console.log("关闭后回调");
		        }
		


	}

}
